import React,{useState, useRef, useEffect} from 'react';
import styled from 'styled-components';
import NumberSlot from '../NumberSlot';

const Wrapper = styled.div`
    h4{
        color:#fff;
        font-weight:700;
        font-size:36px;
        font-family: Segoe;
        font-weight:700;
    }
    .time-block{
        width:fit-content;
    }
    @media (max-width:991px){
        h4{
            font-size: 30px;

        }
    }
    @media (max-width:500px){
        // padding-right:10px;
        h4{
        font-size: 18px;

        }
        .spacing{
            margin-right:5px !important;
        }
    }

   
`;

export default function Index() {
    const [days, setDays] = useState("00")
    const [hours, setHours] = useState("00")
    const [minutes, setMinutes] = useState("00")
    const [seconds, setSeconds] = useState("00")
  

    let interval = useRef();

    useEffect(() =>{
        startTimer()
        return () => {
            clearInterval(interval.current)
        }
    }, [])
    function parseDateString (dateString) {
        var matchers = [];
        matchers.push(/^[0-9]*$/.source);
        matchers.push(/([0-9]{1,2}\/){2}[0-9]{4}( [0-9]{1,2}(:[0-9]{2}){2})?/.source);
        matchers.push(/[0-9]{4}([\/\-][0-9]{1,2}){2}( [0-9]{1,2}(:[0-9]{2}){2})?/.source);
        matchers = new RegExp(matchers.join("|"));
        if (dateString instanceof Date) {
            return dateString;
        }
        if (String(dateString).match(matchers)) {
            if (String(dateString).match(/^[0-9]*$/)) {
                dateString = Number(dateString);
            }
            if (String(dateString).match(/\-/)) {
                dateString = String(dateString).replace(/\-/g, "/");
            }
            return new Date(dateString);
        } else {
            throw new Error("Couldn't cast `" + dateString + "` to a date object.");
        }
    }

    const startTimer = () =>{
        const endDay = "2020-12-31 00:00:00"
        console.log(endDay)
        interval = setInterval(()=>{
            const now = new Date()
            const today = Date.parse(parseDateString(now));
            const countDownDate=Date.parse(parseDateString(endDay));
            const distance = parseInt(countDownDate) - parseInt(today)
         
            const days = Math.floor(parseInt(distance)/(1000 * 60 * 60 * 24))
            const hours = Math.floor(parseInt(distance) % (1000* 60 * 60 * 24)/ (1000 * 60 * 60))
            const minutes = Math.floor(parseInt(distance) % (1000* 60 * 60) / (1000 * 60))
            const seconds = Math.floor(parseInt(distance) %(1000* 60 )/ 1000)

            if(distance < 0){
                clearInterval(interval.current)
            }else{
                // setDays(days.toString().length < 2 ? "0"+days.toString() : days.toString())
                setDays(days.toString().length < 2 ? "0" +days.toString() : days.toString())
                setHours( hours.toString().length < 2 ? "0"+hours.toString() : hours.toString())
                setMinutes( minutes.toString().length < 2 ? "0"+minutes.toString() : minutes.toString())
                setSeconds( seconds.toString().length < 2 ? "0"+seconds.toString() : seconds.toString())
            }   
        },1000)

        
    }
    // console.log(hours)
    return (
        <Wrapper className="countdown">
            <div className="row">
                <div className="col-lg-11 mx-auto text-white">
                    <div className="row">
                        <div className="day col-4 mb-3 mb-md-5 mb-lg-0">
                            <div className=" d-flex time-block mx-auto mb-2 mb-lg-4">
                                <div className="mr-3 spacing">
                                    <NumberSlot value={days[0]}/>
                                </div>
                                <div className="">
                                    <NumberSlot value={days[1]}/>
                                </div>
                            </div>
                            <h4 className="text-center">DAYS</h4>
                            
                        </div>

                        <div className="hour col-4 mb-3 mb-md-5 mb-lg-0">
                            <div  className=" d-flex  time-block  mx-auto mb-2 mb-lg-4">
                                <div className="mr-3 spacing">
                                    <NumberSlot value={hours[0]}/>
                                </div>
                                <div className="">
                                    <NumberSlot value={hours[1]}/>
                                </div>
                            </div>
                            <h4 className="text-center">HOURS</h4>
                            
                        </div>

                        <div className="minute col-4  mb-lg-0">
                            <div  className=" d-flex time-block mx-auto mb-2 mb-lg-4">
                                <div className="mr-3 spacing">
                                    <NumberSlot value={minutes[0]}/>
                                </div>
                                <div className="">
                                    <NumberSlot value={minutes[1]}/>
                                </div>
                            </div>
                            <h4 className="text-center">MINUTES</h4>
                        </div>
                    </div>
                </div>
            </div>

        </Wrapper>
    )
}
