import styled from 'styled-components';

export const Wrapper = styled.div`
    .program-desc{
        font-size:40px;
        font-weight:700;
        color:#fff;
        margin-bottom:30px;
    }
    .platform-desc{
        color:#fff;
        padding:40px 0 50px 0;
        img{
            width:34px;
            margin-bottom:0px;
        }
    }
    .terms{
        color:#fff;
        font-family: Lato;
        font-size: 16px;
        line-height: 35px;


        p{
            color:#fff;
            font-weight: normal;
        }
    }
    
    
@media (max-width: 991px){
   
    .program-desc{
        font-size:32px;
    }
}

@media (max-width: 500px){
    .program-desc{
        font-size:30px;
    }
    
}
}
`;