import React from 'react';
import styled from 'styled-components';
import Button from '../../components/Buttons';

const Wrapper = styled.div`
    position:fixed;
    z-index:3000;
    top:0;
    left:0;
    right:0;
    bottom:0;
    // background:rgba(0,0,0,.9);
    .text-desc{
        font-weight: bold;
        font-size: 30px;
        line-height: 40px;
        text-align: center;
        color: #FFFFFF;
        margin-bottom:60px;
    }

    // background-color: rgba(133, 18, 40, 0.7);
    // background-image: linear-gradient(45deg, rgba(0,0,0,.9), rgba(0,0,0,.9));
    // background-blend-mode: multiply;

    background-color: rgba(0, 0, 0, .95);
    background-image: linear-gradient(45deg, rgba(133, 18, 40, 0.2), rgba(133, 18, 40, 0.2));
    background-blend-mode: multiply;

`;

export default function Index({action}) {
    return (
        <Wrapper>
            <div className="d-flex align-items-center justify-content-center text-white h-100">
                <div className="container">
                    <div className="row">
                        <p className="text-center text-desc col-lg-9 mx-auto">Your registration is successful. We will get in touch with you.</p>
                    </div>
                    <Button link="/index" text="Close" type="modal" action={() => action()}/>
                </div>
                
            </div>
        </Wrapper>
    )
}
